:root{
  --float-button:0%;
}
.App {
  text-align: center;
  height:300vh;
  background-color: rgb(232, 234, 236);

}
.showSettingsBtn{
  position: absolute;
  z-index: 15;
  left:var(--float-button);
  float:left;
  background-color: white;
    border: none;
    cursor: pointer;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.settingsAndEvents{
  margin: 0%;
  padding: 0%;
}